<template>
  <div class="col" :class="classes">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name:'Col',
  props: {
    span: Number
  },
  computed: {
    classes() {
      const cls = [];
      if (this.span) {
        cls.push(`col-${this.span}`);
      }
      return cls;
    }
  }
};
</script>
