      </yd-cell-item>
      <yd-cell-item arrow>
        <span slot="left">背景图片</span>
      </yd-cell-item>
      <yd-cell-item arrow>
        <span slot="left">性别</span>
        <span slot="right">{{user.gender}}</span>
      </yd-cell-item> 
    </yd-cell-group>
