<template>
  <header class="header">
    <div class="back">
      <slot name="left">
        <i @click="$router.back()" class="icon icon-angle-left"></i>
      </slot>
    </div>
    <div class="center">
      <slot name="center">
        <h1 class="title">{{ title }}</h1>
      </slot>
    </div>
    <div class="right">
      <slot name="right"></slot>
    </div>
  </header>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      default: "标题"
    }
  },
  name: "Header"
};
</script>
