<template>
  <div class="row" :class="classes">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'Row',
  props: {
    justify: String,
    align: String,
    // directon: String
  },
  computed:{
    classes(){
      const cls = []
      if (this.justify) {
        cls.push(`row-justify-${this.justify}`)
      }
      if (this.align) {
        cls.push(`row-align-${this.align}`)
      }
      return cls;
    }
  }
}
</script>

